body
{
	margin:0px;
	margin-top:50px;
	font-family:cursive;
}
header
{
	display:flex;
	flex-direction: row;
	position:fixed;
	top:0px;
	left:0;
	right:0;
	height:50px;
	line-height:50px;
	background-color:#eee;
}
header *
{
	display:inline;
	height:50px;
}
header ul
{
	padding:0px;
}
header li
{
	margin-left:20px;
}
section
{
	height:100vh;
	border:1px solid black;
	display:flex;   
	list-style-position: inside;
	list-style-type: decimal;
	justify-content:center;
	align-items:center;
	text-align:center;
	background-size:cover;
	background-position:center center;
	background-repeat:no-repeat;
	background-attachment:fixed;
}
#name .profile-img
{
	width: 25%;
	border-radius:50%;
}
footer
{
	text-align:center;
	padding:50px;
    background-color:aliceblue;
}

#name h1
{
	font-size:3em;
}
section h2
{
	font-size:2.5em;
}
section h3
{
	font-size:1.5em;
}
header a
{
	text-decoration:none;
	color:black;
}
#name{
    background-image: linear-gradient(rgba(255,255,255,0.75),rgba(255,255,255,0.75)), url('./Components/Body/Sections/Images/Background.jpg');
}

#about {
    background-image: linear-gradient(rgba(255,255,255,0.75),rgba(255,255,255,0.75)), url('./Components/Body/Sections/Images/Background.jpg');
}

#contact {
    background-image: linear-gradient(rgba(255,255,255,0.75),rgba(255,255,255,0.75)), url('./Components/Body/Sections/Images/Background.jpg');
}

#current {
	background-image: linear-gradient(rgba(255,255,255,0.75) , rgba(255,255,255,0.75)) , url('./Components/Body/Sections/Images/Background.jpg');
}